import React from 'react';
import styled from 'styled-components';
import { TransactionResponse } from "@ethersproject/providers";
import Web3ReactManager from './components/Web3ReactManager';
//import { LiquidityGenerationEvent } from './pages/LiquidityGenerationEvent';
import { Switch, Route, HashRouter } from 'react-router-dom';
import { useToast } from './components/ToastProvider';
//import { Footer } from './components/Footer';
import { IconType } from './components/Icon/IconType';
import TransactionContext from './contexts/TransactionContext';
//import { Overview } from './pages/Overview';
import { ComingSoon } from './pages/ComingSoon';
import { ExternalLink } from './components/Link';

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
`

const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1;
  min-height: calc(100vh - 4em);
`

const Footer = styled.div`
  color: ${({ theme }) => theme.text2};
  font-size: 0.875em;
  text-align: center;
  display: grid;
  align-items: center;
  justify-items: center;
  width: 100%;
  bottom: 0;
  position: relative;
  padding: 1rem;

`

export const App = () => {
  const { addToast } = useToast();
  const addPendingTransaction = (description: string, txResponse: TransactionResponse) => {
    txResponse.wait().then(txReceipt => {
        addToast(
            txReceipt.status === 1 ? IconType.Success : IconType.Failure,
            `${description} ${txReceipt.status === 1 ? "Completed" : "Failed"}`,
            txReceipt.transactionHash)
    })
} 
  return (
    <HashRouter>     
      <AppWrapper>
        <TransactionContext.Provider value={{ addPendingTransaction }}>        
          {/* <HeaderWrapper>
            <Header />
          </HeaderWrapper> */}
          <BodyWrapper>
            <Web3ReactManager>              
                <Switch>
                  <Route exact strict path="/" component={ComingSoon} />
                  {/* <Route exact strict path="/" component={Overview} />
                  <Route exact strict path="/claim" component={Claim} />
                  <Route exact strict path="/lge" component={LiquidityGenerationEvent} /> */}
                </Switch>              
              </Web3ReactManager>
          </BodyWrapper>
          <Footer>
            <span>From the creators of <ExternalLink href="https://rootkit.finance/">Rootkit</ExternalLink> and <ExternalLink href="https://unifund.global/">Unifund</ExternalLink></span>
          </Footer>
          {/* <Footer /> */}
        </TransactionContext.Provider>
      </AppWrapper>
  </HashRouter>
  );
}

export default App;
