import React from "react";
import styled from "styled-components";
import { padNumber } from "../../utils/format";
import Logo from "../../assets/images/logo.png";
import Countdown from "react-countdown";

const Wrapper = styled.div`
  padding: 9em 1em 1em 1em;
  display: grid;
  grid-gap: 1.5em;
  max-width: 50em;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    max-width: 100%;
    font-size: 0.75em;
    padding: 3em 1em 1em 1em;
 `};
`;

const Title = styled.div`
  font-size: 3em;
  text-align: center;
  ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size: 2.3em;
`};
`;

const Text = styled.div`
  padding-top: 1em;
  font-size: 1.25em;
  text-align: center;
  color: ${({ theme }) => theme.primary1};
`;

const CountdownGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1.75em 0.5em) 1.75em;
  font-size: 3em;
  align-items: center;
  justify-items: center;
  justify-self: center;
`;

const Subtitle = styled.div`
  padding: 0.25em;
  font-size: 0.875rem;
  text-align: center;
`;

const Description = styled.div`
  padding: 0.25em;
  line-height: 1.25em;
  text-align: justify;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    font-size: 1.15em;
 `};
`;

const CountdownWrapper = styled.div`
  justify-self:center;
  padding-bottom 2rem;
  color: ${({ theme }) => theme.primary1};
`;

const LogoText = styled.div`
  font-size: 3.5em;
  white-space: nowrap;
  width: fit-content;
  font-weight: 600;

  ${({ theme }) => theme.mediaWidth.upToSmall`
  font-size: 3em;
`};
`;

const LogoWrapper = styled.div`
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1em;
  align-items: center;
  justify-content: center;
  user-select: none;
`;

const LogoImage = styled.img`
  width: 94px;

  ${({ theme }) => theme.mediaWidth.upToSmall`
    width:70px;
  `};
`;

const Renderer = ({
  days,
  hours,
  minutes,
  seconds,
}: {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
}) => {
  return (
    <CountdownWrapper>
      <CountdownGrid>
        <div>{padNumber(days)}</div>
        <div>:</div>
        <div>{padNumber(hours)}</div>
        <div>:</div>
        <div>{padNumber(minutes)}</div>
        <div>:</div>
        <div>{padNumber(seconds)}</div>
      </CountdownGrid>
      <CountdownGrid>
        <Subtitle>days</Subtitle>
        <div />
        <Subtitle>hours</Subtitle>
        <div />
        <Subtitle>minutes</Subtitle>
        <div />
        <Subtitle>seconds</Subtitle>
      </CountdownGrid>
    </CountdownWrapper>
  );
};

export const ComingSoon = () => {
  return (
    <Wrapper>
      <LogoWrapper>
        <LogoImage src={Logo} alt="logo" />
        <LogoText>upTether</LogoText>
      </LogoWrapper>
      <Title>It goes up against Tether</Title>
      <Text>Market Generation Event starts in</Text>
      <Countdown date={"2021-04-12T20:20:00.000Z"} renderer={Renderer} />
      <Description>
        upTokens are ERC-20 tokens that are mathematically engineered to go up
        against their equivalent counterparts using ERC-31337's token standard
        technology.
      </Description>
      <Description>
        upTether is the first of many upTokens. ROOT holders will vote in the
        DAO to decide on the next upToken being launched and its parameters.
      </Description>
      <Description>
        Our Market Generation Event (M.G.E.) is the fairest possible way to
        raise funds with any token. It creates a permanently locked Uniswap
        trading pair using all collected value and distributes 90% of funds
        raised back to contributors. All this is done without affecting the
        market liquidity. Extra features include a built-in referral system and
        customizable vesting period
      </Description>
    </Wrapper>
  );
};
